.LogoutPage {
    margin-block-start: 20px;    
    width: auto;
    height: auto;
    padding: 40px;
    background-color: #f2f2f2;
    border-radius: 8px;
}

.LogoutPage h1 {
    margin: 0;
    padding: 0;
}

.LogoutPage form {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
    align-items: flex-start;
}

.LogoutPage form>div {
    width: 100%;
}

.LoginPage form button {
    width: max-content;
    padding: 6px 10px;
    border: 1px solid #5f59f7;
    border-radius: 6px;
    text-decoration: none;
    text-transform: capitalize;
    cursor: pointer;
    transition: background-color .3s ease, color .3s ease;
}

.LoginPage form button:hover {    
    background-color: #5f59f7;
    color: white;  
}